.iphone__container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  font-size: 12px;
  gap: 0.6rem;
  font-family: var(--font-principal);
  margin: 2rem;
}

.iphone__container>a{
  font-size: 14px ;
}

.iphone__container>p{
  font-size: 16px;
  text-align: center;
  padding: 0px 0.6rem;
}

.iphone__container>h3{
  font-weight: 100;
  color: var(--naranja);
}

.iphone__container>img{
  height: 260px;


}

.division{
  width: 180px;
  color: black;
  height: 0.5px;
}

.slogan{
  height: 3rem;
}