.container__nav{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.2rem;
  background-color: var(--gris-nav);
  color: white;
  font-family: var(--font-principal);
  font-weight: 200;
  height: 44px;
  font-size: 20px;
  z-index: 20;

  
}

.list__nav{
  display: flex;
  justify-content: space-around;
  gap: 3.2rem;  
}

.list__nav>li{
  display: flex;
}


.list__nav>li>a{
  color: var(--gris-nav-link);
  font-size: 14px;
  transition: 0.6s;
}

.list__nav>li>a:hover{
  color: white;
}

.search-ico{
  cursor: pointer;
}
