.magsafe__container{
  display: flex;
  flex-wrap: wrap;
  margin: 2rem;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 2rem 0.4rem;
  width: 100vw;
}

.magsafe__container>img{
  width: 40vw;
}


.magsafe__texto{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 8rem;
  font-size: 28px;
  font-family: var(--font-principal);
  font-weight: 400;
  
}

.magsafe__texto>h1{
  font-size: 48px;
}