:root{
  --gris-nav :#303030;
  --gris-nav-link: #ababab;
  --azul-link: #0066cc;
  --naranja: #bf4800;
  --azul-btn: #0071e3;
  --font-principal: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --search-bg:#191919;
  --search-ph: #6e6e73;
  --gris-phonenav: #fbfbfd;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: #f2f2f2;
}

a{
  color: var(--azul-link);
  text-decoration: none;
}

li{
  list-style-type: none; 
}

.none{
  display: none !important;
  transition: 1s;
}

button{
  color: white;
  background-color: var(--azul-btn);
  border: none;
  padding: 0.3rem 0.5rem;
  border-radius: 15px;
}

.section_iphones{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: baseline;
}

.comprar{
  display: flex;
  align-items: center;
  justify-content: center;
}

.comprar>a{
  margin:4rem;
  font-family: var(--font-principal);
  font-size: 21px;
}