.phone__container{
  display: flex;
  flex-direction: column;
  font-size: 12px ;
  justify-content: center;
  align-items: center;
  font-family: var(--font-principal);
}

.phone__container>img{
  width: 45px;
  height: 45px;
  
}

.phone__container>h3{
  font-weight: 100;
  cursor: pointer;
}

.phone__container>h3:hover{
  color: var(--azul-link);
}

.phone__container>h5{
  font-weight: 300;
  color: var(--naranja);
}