.container__navSearch{
  display: flex;
  justify-content: center;
  background: var(--search-bg);
  color: var(--search-ph);
  z-index: 10;
  
}

.left{
  display: flex;
  align-items: center;
  z-index: 20;
}

.input-search{
  background-color: transparent;
  border: none;
  font-size: 17px;
  color: white;
  width: 42rem;
  padding-left: 1rem;
  outline: none;
  font-weight: 100;
  z-index: 10;
}



.input-search::placeholder{
  padding-left: 1rem;
  color: var(--search-ph);

}

.quitar-icon{
  cursor: pointer;
  z-index: 10;
}

.enlaces-rapidos{
  position: absolute;
  display: inline-block;
  margin: 0 20%;
  align-items: center;
  justify-content: start;
  background-color: white;
  padding: 2rem;
  width: 50rem;
  border-radius: 0 0 2rem 2rem;
  font-family: var(--font-principal);
  font-weight: 200;
  z-index: 10;

}

.enlaces-rapidos>ul>li{
  margin: 1rem;
  
}

.enlaces-rapidos>h3{
  color: var(--search-ph);
  font-size: 16px;
  font-weight: 200;
}


